<template>
	<div id="seckill-index">
		<public-header title="限时抢购"></public-header>
		<div class="content">
			<van-swipe :autoplay="3000" v-if="slides.length > 0">
				<van-swipe-item v-for="(slide,index) in slides" :key="index">
					<div @click="util.jsUrl(slide.link)">
						<img :src="slide.thumb" alt="">
					</div>
				</van-swipe-item>
			</van-swipe>
			<div class="store">
				<div class="store-tabs">
					<div class="tab-item" :class="{active: cid == 0}" @click="getCidGoods(0)">全部</div>
					<div class="tab-item" :class="{active: cid == item.id}" v-for="(item,index) in goods_category" @click="getCidGoods(item.id)" :key="index">{{item.title}}</div>
				</div>
			</div>
			<van-list v-model="goods.loading" :finished="goods.loaded" :offset="100" @load="onGetGoods" :immediate-check="false" v-if="!goods.empty">
				<div class="list">
					<div class="list-item" :class="{'van-hairline--bottom': index < goods.data.length -1 }" v-for="(list,index) in goods.data">
						<div class="img-container">
							<img class="item-logo" :src="list.thumb" alt="">
							<img class="item-sign" src="static/img/qiang.png" alt="">
						</div>
						<div class="item-info">
							<div class="top">
								<div class="title">{{list.title}}</div>
								<div class="store-title">
									<van-icon name='shop'></van-icon>
									{{list.store_title}}
								</div>
								<div class="stock">仅剩{{list.total}}份</div>
							</div>
							<div class="bottom">
								<div class="left">
									<div class="price">￥<span>{{list.price}}</span></div>
									<div class="old-price">¥{{list.old_price}}</div>
								</div>
								<div class="right">
									<router-link class="seckill-go" :to="util.getUrl({path: '/pages/seckill/detail', query: {id: list.id}})">去抢购</router-link>
									<div class="progress">
										已售{{list.sailed_percent}}%
										<div class="progress-bar">
											<div class="progress-bar-inner" :style="{width: list.sailed_percent + '%'}"></div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
				<div class="loaded" v-if="goods.loaded">
					<div class="loaded-tips">我是有底线的</div>
				</div>
			</van-list>
			<load type="loaded" text="暂无符合条件的商品" v-else></load>
		</div>
		<transition name="loading">
			<iloading v-if="showPreLoading"></iloading>
		</transition>
	</div>
</template>

<script>
	import Load from '@/components/load'
	import PublicHeader from '@/components/header'
	export default {
		components: {
			PublicHeader,
			Load
		},
		data() {
			return {
				showPreLoading: true,
				cid: 0,
				sid: 0,
				slides: [],
				goods_category: [],
				goods: {
					page: 1,
					psize: 15,
					loading: false,
					loaded: false,
					empty: false,
					data:[]
				}
			}
		},
		methods: {
			onLoad(){
				this.util.request({
					url: 'seckill/seckill',
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message)
						return;
					}
					this.goods_category = result.message.categorys;
					this.slides = result.message.slides;
					this.onGetGoods();
				})
			},
			onGetGoods() {
				if(this.goods.loaded) {
					return false;
				}
				this.util.request({
					url: 'seckill/seckill/goods',
					data: {
						cid: this.cid,
						sid: this.sid,
						page: this.goods.page,
						psize: this.goods.psize,
					}
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message)
						return;
					}
					this.goods.page++;
					this.goods.data = [...this.goods.data, ...result.message.goods];
					if (!this.goods.data.length) {
						this.goods.empty = true;
					}
					if(result.message.goods.length < this.goods.psize) {
						this.goods.loaded = true;
					}
					this.goods.loading = false;
				})
			},
			getCidGoods(id) {
				if(id == this.cid) {
					return false;
				} else {
					this.cid = id;
				}
				this.goods = {
					page: 1,
					psize: 15,
					loading: true,
					loaded: false,
					empty: false,
					data:[]
				};
				this.onGetGoods();
			}
		},
		created() {
			if(this.$route.query.sid) {
				this.sid = this.$route.query.sid
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#seckill-index .content{
		position: fixed;
		top: 46px;
		right: 0;
		left: 0;
		bottom: 0;
		width: 100%;
		background: #fff;
		overflow-y: scroll;
	}
	#seckill-index .content::-webkit-scrollbar {
		display: none;
	}
	#seckill-index .content .van-swipe img{
		width: 100%;
		height: 150px;
	}
	#seckill-index .content .van-swipe .van-swipe-item{
		font-size: 0;
	}
	#seckill-index .content .store{
		overflow-x: scroll;
	}
	#seckill-index .content .store .store-tabs{
		position: relative;
		width: 100%;
		height: 45px;
		overflow-x: scroll;
		white-space: nowrap;
		background-color: #fff;
		color: #333;
	}
	#seckill-index .content .store .store-tabs .tab-item{
		display: inline-block;
		height: 100%;
		width: 20%;
		margin: 0;
		line-height: 45px;
		text-align: center;
		font-size: 14px;
		position: relative;
	}
	#seckill-index .content .store .store-tabs .tab-item.active{
		color: #f44;
	}
	#seckill-index .content .store .store-tabs .tab-item.active:after{
		content: '';
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		height: 0;
		border-bottom: 2px solid #f44;
	}
	#seckill-index .content .list{
		padding: 0 10px;
	}
	#seckill-index .content .list .list-item{
		width:100%;
		height:130px;
		padding:10px 0;
		box-sizing:border-box;
		display:flex;
		flex-flow:row nowrap;
		justify-content:space-between;
		line-height:1;
	}
	#seckill-index .content .list .list-item .img-container{
		position: relative;
	}
	#seckill-index .content .list .list-item .img-container .item-logo{
		width: 110px;
		height: 110px;
		border: none;
	}
	#seckill-index .content .list .list-item .img-container .item-sign{
		position:absolute;
		top:0;
		left:0;
		width:50px;
		height:50px;
	}
	#seckill-index .content .list .list-item .item-info{
		width:64.5%;
		position:relative;
		display:flex;
		flex-direction:column;
		justify-content:space-between;
	}
	#seckill-index .content .list .list-item .item-info .top{
		width: 100%;
	}
	#seckill-index .content .list .list-item .item-info .top .title{
		width:100%;
		font-size:16px;
		overflow:hidden;
		text-overflow:ellipsis;
		white-space:nowrap;
		line-height: 16px;
	}
	#seckill-index .content .list .list-item .item-info .top .store-title{
		margin:7px 0;
		color:rgba(137, 137, 137, 0.9);
		font-size: 14px;
		line-height: 14px;
	}
	#seckill-index .content .list .list-item .item-info .top .store-title .van-icon-shop{
		font-size: 14px;
	}
	#seckill-index .content .list .list-item .item-info .top .stock{
		color:#ff2d4b;
		font-size: 14px;
		line-height: 14px;
	}
	#seckill-index .content .list .list-item .item-info .bottom{
		display:flex;
		flex-flow:row nowrap;
		justify-content:space-between;
	}
	#seckill-index .content .list .list-item .item-info .bottom .left{
		display:flex;
		flex-direction:column;
		justify-content:flex-end;
		width: 32%;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	#seckill-index .content .list .list-item .item-info .bottom .left .price{
		color:#ff2d4b;
		font-size:14px;
		margin-bottom:7px;
		line-height: 14px;
	}
	#seckill-index .content .list .list-item .item-info .bottom .left .price span{
		font-size: 20px;
	}
	#seckill-index .content .list .list-item .item-info .bottom .left .old-price{
		font-size:12px;
		color:#cacaca;
		text-decoration:line-through;
		line-height: 12px;
	}
	#seckill-index .content .list .list-item .item-info .bottom .right{
		overflow: hidden;
		width: 67%;
	}
	#seckill-index .content .list .list-item .item-info .bottom .right .seckill-go{
		display: block;
		width:85px;
		height:32px;
		border-radius:4px;
		line-height:32px;
		text-align:center;
		background-color:#ff2d4b;
		color:#fff;
		margin-bottom:5px;
		float: right;
		font-size: 15px;
	}
	#seckill-index .content .list .list-item .item-info .bottom .right .progress{
		font-size:12px;
		color:#848689;
		line-height: 12px;
		width: 100%;
		word-wrap: break-word;
		overflow: hidden;
	}
	#seckill-index .content .list .list-item .item-info .bottom .right .progress .progress-bar{
		width:48%;
		height:8px;
		border-radius:5px;
		border:1px solid #f36262;
		overflow:hidden;
		margin-top: 2px;
		float: right;
	}
	#seckill-index .content .list .list-item .item-info .bottom .right .progress .progress-bar .progress-bar-inner{
		height:100%;
		background-color:#ff8989;
	}
	#seckill-index .content .loaded .loaded-tips{
		background: #fff;
	}
</style>


